.nav_second{
    height: 50px;
    border-top: 1px solid lightslategray;
    border-bottom: 1px solid lightslategray;
}


.container{
    max-width: 1240px;
    min-width: 840px;
    margin: 0 auto;
}
.header_banner{
    background-image:url("https://img-c.udemycdn.com/notices/web_banner/image_udlite/1cddd624-4f04-4c1e-98e5-14d86e65185f.jpg?Expires=1621644451&Signature=D8MGaleUrO-SwxghDZUvFQ9b9ex7lJFNpKMBqdeZy~dx2SI3U7qz3jJ09GaUiVlwo~6Ems~fzd3Ggha2~4pYKLObd~Dja~V8nMLJXmZEAVtJOAkXcql8K~b5075nFQp85k121RMRqvXpX~oHn1pNqs3oIaBB3uGIjyeiKJGoQ4UrGb1zvE8JtZWA6G8W9zocOV5XYa1PiDWYZWXNcMkPCpw-aEbhbsC8VGBMr1TrHtuN~ECP4ccBtXynXrr9NUQL8XYpJ57GSjcUyRLaRDliDAvnGlfkeY9fuzA2zg107~cysi-lIxdWy0tNBbPraDY44vqHmr7s9H9o7CwpImab1w__&Key-Pair-Id=APKAITJV77WS5ZT7262A");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 500px;
    margin:0;
    padding: 20px 0px;
}
.header_banner >div{
    position: relative;
    background-color: white;
    width: 400px;
    margin: 0 30px;
    padding: 10px 80px 10px 10px;
    height: auto;
    top: 50%;
    transform: translateY(-50%);
}
.header_banner >div>h2{
    font-size: 3em;
}

.nav_second{
    display: flex;
    flex-direction: row;
    justify-content: center;
    overflow: hidden;
    align-items: center;
}
.nav_second >p{
    display: inline-block;
    font-size: 14px;
    margin-right:10px;
}
@media (max-width: 714px) {
    .nav_second{
        display: none;
    }
}