nav{
    width: 100%;
    height: 80px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.header_logo{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0px;
    padding: 0 10px;
}
.header_logo >img{
    max-width: 200px;
    height: auto;
    margin: 0px 10px;
}
.dropdown{
    position: relative;
}
.dropdown_title{
    line-height: 80px;
    margin: 0px;
    position: relative;
}
.dropdown_title:hover .dropdown_content{
    display: block;
}
.dropdown_content{
    position: absolute;
    border: 1px solid lightslategray;
    border-radius: 10px;
    padding: 0px 10px;
    top: 10;
    left: 0;
    margin: 0;
    z-index: 1;
    background-color: #fff;
    display: none;
}
.header_search{
    flex: 1;
    border-radius: 30px;
    border: 1px solid #333;
    padding: 10px;
    align-items: center;
    justify-content: flex-start;
    display: flex;
}
.search_header{
    margin-left: 10px;
}
.header_search >input{
    width: 100%;
    border: none;
    margin-left: 20px;
}
input:focus{
    outline: none;
}
.header_item{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0px;
    padding: 20px;

}
.header_item >ul>li{
    list-style: none;
    display: inline-block;
    margin-right: 20px;
    padding: 0px;
}
.header_item >ul{
    margin: 0px;
    padding: 0px;
}
.header_icons{
    display: flex;
    align-items: center;
    flex-direction: row;
    margin: 0px;
    padding: 0px;
}
.icons_favourite, .icons_bell, .icons_cart{
    padding: 0px;
    margin-right: 10px;
}
.title{
    background-color: green;
    padding: 10px;
    border: none;
    border-radius: 50%;
    color: white;
}
@media (max-width: 714px){
    .header_item > ul{
        display: none;
    }
    .icons_bell, .icons_favourite{
        display: none !important;
    }
    .title {
        display: none;
    }
    .header_search >input{
        display: none;
    }
    .header_search{
        border: none;
    }
    .dropdown_title{
        display: none;
    }
    .search_header{
     float: right !important;
    }
}