footer{
    display: grid;
    grid-template-rows: auto auto auto;
    grid-row-gap: 20px;
    align-content: center;
    margin: 0px;
}
.footer_top{
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid lightslategray;
    border-bottom: 1px solid lightslategray;
    margin: 0px;
    padding: 0px 20px;
}
.footer_top>h2{
    font-size: 1em;
}
.icon_footer{
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    align-items: center;
}
.icon_footer img{
    margin-right: 20px;
}
.footer_middle{
 display: grid;   
 grid-template-columns: auto auto;
 justify-content: space-between;
 margin: 0px;
    padding: 0px 20px;
}
.footer_content{
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: space-between;
    align-items: flex-start;
}
.left_content, .Middle_content, .right_content{
    margin-right: 20px;
}
ul{
    list-style: none;
    margin: 0px;
    padding: 0px;
}
li{
    text-align: left;
    margin: 10px 0px;
}
.footer_language{
    align-items: flex-start;
}
.language_footer{
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    border: 1px solid lightslategray;
    padding: 0px 10px;
    max-width: 100px;
    border-radius: 15px;
}
.language_footer >p{
    margin: 0px 10px 0px 5px;
    padding: 10px;
    border-left: 1px solid lightslategray;
}

.footer_bottom{
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    margin: 0px;
    padding: 0px 20px;
}
.footer_logo > img{
max-width: 200px;
height: auto;
}